<template>
    <div class="mobile-valid">
      <pwd-crumbs></pwd-crumbs>
      <form action="" @submit.prevent="submit" class="comForm">
        <div class="input-wrapper">
          <input type="text" v-model="sendForm.queryMobile" :class="errorMobile?'active':''"  @focus="focus($event,'mobile')" @blur="blur($event,'mobile')" placeholder="手机号">
          <i class="del" v-show="sendForm.queryMobile" @mousedown.stop="del('queryMobile')"  :style="delImg"></i>
          <span class="error" >{{errorMobile}}</span>
        </div>
        <div class="input-wrapper msgcode-wrapper clearfix">
          <div class="msgCode-layer" :class="errorMsgCode?'errorMsgCode':''">
            <input type="text" v-model="sendForm.msgCode"   @focus="focus($event,'msgCode')" @blur="blur($event,'msgCode')" placeholder="短信验证码">
            <i class="del" v-show="sendForm.msgCode" @mousedown.stop="del('msgCode')"  :style="delImg"></i>
            <span class="aboutcode" v-if="!isMessageCode" @click="getMessageCode()">发送验证码</span>
            <span class="aboutcode sendcode" v-else>{{second}}s重发</span>
            <span class="error " >{{errorMsgCode}}</span>
          </div>
        </div>

        <div class="submit-wrapper">
          <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
          <input type="submit" value="下一步" class="submit" :class="allValid? 'active':''" :disabled="!allValid">
        </div>
        <div class="go-login-tips">
          已有账号，马上<router-link tag="span" to="/smsLogin" class="register-btn"><span>登录</span></router-link>
        </div>
        <img-code v-if="imgCodeShow"
                  :absTop="251"
                  :absLeft="90"
                  @hanleClose="hanleImgCodeClose">
          <div class="imgcode-content">
            <div class="input-wrapper">
              <input type="text" class="input-imgcode" placeholder="请输入图形验证码"
                     @focus="focus($event,'imgCode')" @blur="blur($event,'imgCode')" v-model.trim="sendForm.queryImgCode">
              <i class="del" v-show="sendForm.queryImgCode" @mousedown.stop="del('queryImgCode')" :style="delImg"></i>
              <img ref="imgCode" :src="imgCodeSrc" alt="图形验证码" class="imgcode" @click="refreshImgcode">
              <span class="error">{{errorImgCode}}</span>
            </div>
            <input type="button" class="imgcode-btn" value="确定"
                   @click="validImgcode"
                   :class="sendForm.queryImgCode.length>=4?'active':''"
                   :disabled="sendForm.queryImgCode.length<4"
            >
          </div>
        </img-code>
      </form>
    </div>
</template>

<script>
import imgCode from '@/components/base/TheImgCode.vue'
import pwdCrumbs from '@/components/password/ThePwdCrumbs'
import { getComFormMixin } from '@/assets/js/mixins/comForm'
import { ValidateUtil } from '@/assets/js/global'
import { mobileErrorTips } from '@/assets/js/errorTips'
import { setLocalStorage, getLocalStorage } from '@/assets/js/localStorage'
export default {
  name: 'TheMobileVailid',
  mixins: [getComFormMixin],
  data () {
    return {
      imgCodeSrc: '',
      second: 60,
      sendForm: {
        queryMobile: '',
        msgCode: '',
        queryImgCode: ''
      }
    }
  },
  mounted () {
    this._isCountDown()

  },
  methods: {
    submit () {
      let postData = {
        mobile: this.sendForm.queryMobile,
        captchaId: this.sendForm.msgCode,
        hiddenCaptcha: this.uuid
      }
      this.$api.user.mobileValidCommit(postData).then(res => {
        if (res.code === 1) {
          this.$router.push({
            path: '/forgetPassword/identifyValid'
          })
        } else {
          if (res.message.includes('短信验证码错误')) {
            this.errorMsgCode = res.message
          } else {
            this.errorTips = res.message
          }
        }
      })
    },
    getMessageCode () {
      if (this.errorMobile) return
      if (!this.sendForm.queryMobile) {
        this.errorMobile = mobileErrorTips.notNull
        return
      }
      this.imgCodeShow = true
      this.refreshImgcode()
      this.sendForm.queryImgCode = ''
    },
    sendCaptchaSms () {
      let postData = {
        mobile: this.sendForm.queryMobile,
        captchaId: this.uuid
      }
      this.$api.user.forgetPwdSendCaptchaSms(postData).then(res => {
        this.readsecond()
      })
    },
    validImgcode () {
      let postData = {
        code: this.sendForm.queryImgCode
      }
      this.$api.user.forgetPwdValidImgcode(postData).then(res => {
        if (res.code === 1) {
          this.imgCodeShow = false
          this.sendCaptchaSms()
        } else {
          this.refreshImgcode()
          this.sendForm.queryImgCode=''
          this.errorImgCode = res.message
        }
      })
    },
    isMobileRegister () {
      let postData = {
        mobile: this.sendForm.queryMobile
      }
      this.$api.user.isMobileRegister(postData).then((res) => {
        if (res.code === 0) {
          // 1：未注册 0：注册
          this.errorMobile = mobileErrorTips.notRegister
        }
      })
    },
    refreshImgcode () {
      let _random = Math.random()
      this.imgCodeSrc = `${process.env.VUE_APP_BASE_URL}/member/register/captcha?v=${_random}`
    },
    blur (event, name) {
      this._onBlur(event)
      switch (name) {
        case 'mobile':
          this._validMobile()
          break
        // case 'pwd':
        //   this.errorPwd = ValidateUtil.pwdRegexs(this.pwdLoginForm.pwd)
        //   break
      }
    },
    readsecond () {
      this.isMessageCode = true
      var timer = setInterval(() => {
        if (this.second <= 1) {
          this.isMessageCode = false
          this.second = 60
          clearInterval(timer)
          return
        }
        this.second--
        setLocalStorage('forgetSecond', this.second)
      }, 1000)
    },
    _isCountDown () {
      let sec = getLocalStorage('forgetSecond')
      if (sec > 1) {
        this.second = sec
        this.isMessageCode = true
        this.readsecond()
      }
    },
    hanleImgCodeClose () {
      this.imgCodeShow = false
    },
    _validMobile () {
      if (this.sendForm.queryMobile) {
        if (!ValidateUtil.mobileRegexs(this.sendForm.queryMobile).pass) {
          this.errorMobile = ValidateUtil.mobileRegexs(this.sendForm.queryMobile).tip
          return
        }
        this.isMobileRegister()
      }
    }
  },
  computed: {
    allValid () {
      return this.sendForm.queryMobile && this.sendForm.msgCode && !this.errorMobile
    }
  },
  components: {
    pwdCrumbs,
    imgCode
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";
  .mobile-valid{
    background: #fff;
    min-height: 100%;
  }
</style>
